@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Position";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Color";

.presentation {
    .backButton {
        @include Position.set($z-index: epsilon);
        @include Padding.set($preset: gamma);
    }
    .finishButton {
        width: Layout.get-max-width(alpha);
    }

    .stepSection {
        width: Layout.get-max-width(alpha);
        white-space: initial;
    }

    [aria-current="true"] {
        @include Color.set($bg: "primary");
    }

    [aria-current="false"] {
        @include Color.set($bg: "primary-light");
    }

    .title#{Text.$selector} {
        @include Color.set($fg: "gray-dark");
        @include Font.set(
            $size: kappa,
            $weight: bold,
            $family: "default",
            $align: default,
            $wrap: break
        );
        @include Margin.set($bottom: gamma, $top: gamma);
        user-select: none;
    }

    .subtitle#{Text.$selector} {
        @include Font.set($align: default, $size: iota, $wrap: break);
        @include Margin.set($top: beta);
        user-select: none;
    }
}
